<script>
import * as WxShare from "../_common/js/wx_share";

export default {
  // https://vuejs.org/v2/guide/components.html#Props
  props: {
    initCount: Number,
    initActive: Boolean,
    iconImg: String
  },
  computed: {
    style() {
      // return 'background-color: ' + this.hovering ? this.color: 'red';
    },
    orderToCreate() {
      return this.$store.state.orderToCreate;
    },
    targetProduct() {
      return this.$store.state.targetProduct;
    },
    targetWork() {
      return this.$store.state.targetWork;
    }
  },
  data() {
    return {
      currentActiveCount: 0,
      currentActive: false,
      userName: "",
      telNum: "",
      address: ""
    };
  },
  watch: {},
  created() {
    this.currentActive = this.initActive;
    this.currentActiveCount = this.currentActive
      ? this.initCount
      : this.initCount + 1;
  },
  mounted() {},
  updated() {},
  beforeRouteEnter(to, from, next) {
    // i think need check here, e.g. cookie address
    next();
  },
  methods: {
    toConfirmOrder() {
      // passin orderToCreate TODO
      if (!this.address) {
        alert("");
      } else {
        console.log(this.orderToCreate);
        WxShare.makeWxOrder(
          { ...this.orderToCreate, amount: 1 },
          () => {
            console.log("pay succeed");
          },
          () => {
            console.log("pay fail");
          }
        );
      }
    },
    editAddress() {
      //
      WxShare.createAddress(
        address => {
          this.address = address.detailInfo;
          this.userName = address.userName;
          this.telNum = address.telNumber;
          this.$store.commit("SAVE_ORDER_INFO", { address });
        },
        () => {}
      );
    }
  }
};
</script>

<template>
  <div class="container">
    <div class="order-confirm">
      <div class="address" @click="editAddress">
        <div id="basic-info">
          
          <span>{{userName}}</span>
          <span style="flex:1">{{telNum}}</span>
        </div>
        <div id="address-info">
          
          <span>{{address}}</span>
          <div class="address-underline" id="underline1"></div>
          <div class="address-underline" id="underline2"></div>
        </div>
      </div>
    </div>
    <div class="item">
      <img src alt>
      <div class="goods">
        <p class="sub-title"></p>
        <p>{{this.targetWork.title}}</p>
      </div>
      <div class="amount">
        <span>-</span>
        <input v-model="amount" type="text">
        <span>+</span>
      </div>
    </div>
    <div class="item-list">
      <p class="sub-title"></p>
      <div class="sub-detail">
        <p> x 1</p>
        <p> x 1</p>
        <p> x 1</p>
      </div>
    </div>
    <div class="msg">
      <input v-model="giftMsg" type="text" placeholder="">
    </div>
    <div class="readme">
      <ul>
        <li>7-</li>
        <li></li>
        <li></li>
      </ul>
    </div>
    <div
      style="position:absolute;bottom:40px;width:100%;display: flex;align-items: center;justify-content: center;"
    >
      <div class="purchaseBtn" @click="toConfirmOrder"></div>
    </div>
  </div>
</template>

<style lang="scss" scoped>
@import "../_common/style/_functions.scss";
@import "../_common/style/_variables.scss";
@import "../_common/style/_mixins.scss";
@import "../_common/style/_reboot.scss";
.container {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  background-color: $darker-gray;
}
.order-confirm {
  position: relative;
}
.address {
  position: relative;
  width: getRem(654);
  height: getRem(264);
  background-color: #2c2d30;
  margin: getRem(32) auto;
  text-align: left;
  padding-left: getRem(24);
  #basic-info {
    position: relative;
    color: #8c8c92;
    display: flex;
    font-size: 14px;
    width: 100%;
    height: getRem(100);
    padding-top: getRem(32);
  }
  #address-info {
    position: relative;
    color: #6d6e75;
    font-size: 12px;
    width: 100%;
    height: getRem(100);
    .address-underline {
      position: absolute;
      width: getRem(400);
      height: 1px;
      transform: scaleY(0.5);
      background-color: white;
    }
    #underline1 {
      left: getRem(144);
      top: 0.5rem;
    }
    #underline2 {
      left: getRem(144);
      top: 1.5rem;
    }
  }
}
.item {
  position: relative;
  width: getRem(644);
  height: getRem(128);
  display: flex;
  .product-img {
  }
  .desc {
  }
  .amount {
  }
}
.productPage {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.purchaseBtn {
  background-color: #4564d7;
  width: getRem(640);
  height: getRem(92);
  font-size: 16px;
  color: white;
  text-align: center;
  line-height: getRem(92);
  border-radius: getRem(46);
}
.readme{
  margin:0 getRem(48);
  color:#6D6E75;
  text-align:left;
  font-size:getRem(32);
}
// reset
input{
  -webkit-appearance: none; -moz-appearance: none; -o-appearance: none; appearance: none;
  background:none;
  border:0;
}
.msg{
  padding:getRem(22) 0;
  margin:getRem(16) getRem(48) getRem(72);
  border:1px solid #979797;
  font-size:getRem(24);
  input{
    width:100%;
    text-align:center;
  }
}
.item-list{
  margin-top:getRem(18);
  text-align:left;
  margin-left:getRem(48);
  .sub-title{
    color:#6D6E75;
    font-size:getRem(24);
  }
  .sub-detail{
    color:#6D6E76;
    font-size:getRem(28);
  }
}
.item{
  margin:0 getRem(48);
  border-bottom:1px solid #D8D8D8;
}
.goods{
  color:#8C8C92;
  .sub-title{
    font-size:getRem(36);
  }
}
</style>



